.list_block {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  gap: 10px;
  div {
    //border: 1px solid $table_border__color;
    width: 100%;
		//box-shadow: 0 1px 4px 0 rgba(0,0,0,.2);
  	transition: 0.2s;
		border-radius: 10px;
		padding: 20px 30px;
		background-color: $block_color__background;

		p {
			margin-bottom: 10px;
			color: #005f9f;
		}

    ul {
      //margin-left: 20px;
      li {
        line-height: 31px;
        list-style-type: " - ";
        list-style-position: inside;
      }
    }
		.last_items {
			display: flex;
			flex-wrap: wrap;
  		gap: 20px;
			li {
				flex: 0 0 30%;
				display: block;
				text-align: center;
				align-content: center;
				img {
					margin: 0 auto;
					width: 80%;
				}
				&:last-child {
					img {
					//	width: 40%;
					}
				}
			}
			
		}
  }


  }
